.top-menu {
  flex: 1;
  border: 1px solid rgba(132, 146, 175, 0.3);
  background-color: #ffffff;
  align-items: center;
  justify-content: space-between;
  max-width: 100vw;
  .left {
    margin-left: 40px;
    button:first-of-type {
      margin-right: 20px;
    }
    .arrow {
      height: rem(12px);
      margin-top: 6px;
      transition: transform 0.5s ease;
      &.turn-arrow {
        transform: rotate(180deg);
      }
    }
  }
  button {
    cursor: pointer;
    outline: 0;
    border: 0;
    background-color: unset;
    &.add-project {
      width: 178px;
      height: 48px;
      background-color: #5584ff;
      align-self: center;
      border-radius: 28px;
      color: #ffffff;
      font-family: roboto-regular;
      font-size: rem(18px);
      letter-spacing: 0.24px;
      font-weight: 700;
      &:hover {
        background-color: #4478fc;
      }
    }
  }
  .right {
    margin-right: 53px;
  }
}

@media (max-width: 600px) {
  .top-menu {
    flex-direction: column;

    .left,
    .right {
      margin: 0;
      flex-wrap: wrap;
      .add-project {
      }
    }
  }
}
